.Todo {
    background: rgb(47, 103, 255);
      width: 200px;
      height: auto;
      color: white;
      padding: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      margin-bottom: 5px;
      font-weight: bold;
}

.Todo-task {
    margin: 0;
    width: 80%;
    overflow-wrap: break-word;
}


.Todo button {
    background-color: rgb(255, 110, 110);
    color: #fff;
    border-radius: 20px;
    width: 20px;
    height: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
