.NewTodoForm {
    margin-bottom: 15px;
    display: flex;

    justify-content: space-between;
    align-items: center;
}

input {
    font-size: 16px;
    padding: 3px;
    color: rgb(228, 8, 217);

}

.NewTodoForm button {
    padding: 5px 10px;
    font-size: 14px;
    border-radius: 5%;
    color: rgb(255, 255, 255);
    background-color: rgb(215, 14, 171);
    font-weight: bold;
}


input::placeholder {
    color: rgb(245, 130, 255);
    
}